.start-info{
    height: 665px;
    color: #448844;
    display: flex;
    padding: 72px 80px;
    flex-direction: row;
    justify-content: space-between;
}

.start-info__text{
    width: 640px;
    flex-direction: column;
    justify-content: start;
    text-align: left;
}

.start-info__title{
    font-family: 'Playfair Display', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 56px;
    line-height: 56px;
    margin: 0 0 24px;
}

.start-info__desc{
    font-family: 'Roboto', sans-serif;
    color: #226622;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 128%;
    margin: 0 0 40px;
}

.start-info__quote{
    color: #226622;
    font-family: 'Roboto Condensed', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 31px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
}

.start-info__photo-block{
    background-image: url("../../images/cl.svg");
    background-position: left top;
    background-repeat: no-repeat;
    flex-direction: column;
    margin: 0;
}

.start-info__photo {
    width: 320px;
    height: 320px;
    background-image: url("../../images/IMG_1145.JPEG");
    background-size: contain;
    background-position: top;
    background-repeat: no-repeat;
    margin: 32px 0 0 160px;
}

@media screen and (max-width: 1023px) {
    .start-info {
        flex-direction: column;
    }

    .start-info__text{
        width: 500px;
    }

    .start-info__desc {
        margin-left: 50px;
    }

    .start-info__quote {
        margin-left: 100px;
    }

    .start-info__photo-block {
        margin: 0 auto;
    }

    .start-info {
        background-image: url("../../images/cl.svg");
        background-position: right -150px top -150px;
        background-size: 400px;
        background-repeat: no-repeat;
    }
}

@media screen and (max-width: 767px) {
    .start-info {
        flex-direction: column;
        padding: 40px 30px;
    }

    .start-info__text{
        width: 500px;
    }

    .start-info__desc {
        margin-left: 20px;
    }

    .start-info__quote {
        font-size: 21px;
        line-height: 28px;
        margin-left: 50px;
    }

    .start-info {
        background-position: right -100px top -100px;
        background-size: 300px;
    }
}

@media screen and (max-width: 650px) {
    .start-info {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0 24px;
        background-image: none;
    }

    .start-info__text {
        width: 420px;
        justify-content: center;
        text-align: center;
    }

    .start-info__title {
        font-size: 48px;
        line-height: 50px;
        margin-bottom: 24px;
    }

    .start-info__desc {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
    }

    .start-info__quote {
        margin-left: auto;
        margin-right: auto;
        font-size: 18px;
        line-height: 24px;
    }
}

@media screen and (max-width: 509px) {
    .start-info__photo-block {
        background-image: none;
    }

    .start-info__photo {
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width: 449px) {
    .start-info {
        margin-top: 40px;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        padding:0;
        background-image: url("../../images/cl.svg");
        background-position: right top;
        /*background-size: 3000px;*/
        background-repeat: no-repeat;
    }

    .start-info__text {
        margin-left: 28px;
        width: 286px;
        justify-content: center;
        text-align: left;

    }

    .start-info__title {
        font-size: 40px;
        line-height: 42px;
        margin-top: 0;
        margin-bottom: 24px;
    }

    .start-info__desc {
        font-size: 16px;
        line-height: 20px;
        margin-bottom: 20px;
    }

    .start-info__quote {
        font-size: 18px;
        line-height: 24px;
    }

    .start-info__photo{
        width: 320px;
        height: 320px;

    }

    .start-info {
        background-image: none;
    }
}


